import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import { getAgent, getAgents } from "../../redux/slices/agent";
import { getSupervisorsToSelect } from "../../redux/slices/supervisor";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import AgentNewEditForm from "../../sections/@dashboard/agent/AgentNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function AgentCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { agentKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentAgent, setCurrentAgent] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
    dispatch(getSupervisorsToSelect());
    dispatch(getAgents(1, 1));
  }, [agentKey]);

  const handleGetObject = async () => {
    try {
      const result = await getAgent(agentKey);
      setCurrentAgent(result);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.agent.agent);
    }
  };

  return (
    <Page
      title={`Configuration: ${
        !isEdit ? t("p_vendor.new_vendor") : t("p_vendor.edit_vendor")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={!isEdit ? t("p_vendor.new_vendor") : t("p_vendor.edit_vendor")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_vendor.vendor"),
              href: PATH_DASHBOARD.agent.root,
            },
            {
              name: !isEdit
                ? t("p_vendor.new_vendor")
                : `${t("p_vendor.vendor")} ${currentAgent?.full_name}`,
            },
          ]}
        />

        <AgentNewEditForm isEdit={isEdit} currentAgent={currentAgent} />
      </Container>
    </Page>
  );
}
